@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;700;800&family=Raleway:wght@400;500;600;700&display=swap");

*,
*::after,
*::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Raleway", sans-serif;
}

body {
  width: 100%;
  height: 100vh;
  min-height: 100vh;
  overflow: hidden;
}
body.active {
  overflow: auto;
}

html,
body {
  // For Mozilla
  scrollbar-color: black rgb(59, 55, 55) !important;
  scrollbar-width: thin;

  // For Chrome and other browsers
  ::-webkit-scrollbar {
    background-color: rgb(59, 55, 55);
  }
  ::-webkit-scrollbar-thumb {
    background-color: black;
  }
}

body a {
  text-decoration: none;
}

.mobile-zone {
  display: none;
}

.pace .pace-progress {
  background: black !important;
  height: 5px !important;
}

.container {
  opacity: 0;
  transition: opacity 0.7s ease-out;
  pointer-events: none;
}
.container.active {
  opacity: 1;
  pointer-events: auto;
}

@media all and (max-width: 1004px) {
  body {
    display: grid;
    place-content: center;
    padding: 0 10vw;
  }
  .mobile-zone {
    display: block;
    font-size: clamp(1rem, 5vw, 3rem);
    width: fit-content;
    text-align: center;
    margin-inline: auto;
  }

  .hero-wrapper,
  .promo-wrapper,
  .products-wrapper,
  #skincare-page .hero-wrapper,
  .footer-wrapper {
    display: none;
  }
}
